import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { Label } from "../omgdi/Label";

export class BPMNLabel extends Label {
    constructor(model: BpmnModel) {
        super(model);
    }

    ClrTypeID: string = "bpmn.bpmndi.BPMNLabel";
    NewInstance(): BPMNLabel {
        return new BPMNLabel(this.Model);
    }

    LabelStyle: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.LabelStyle = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "LabelStyle", this.LabelStyle);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.LabelStyle = obj.LabelStyle;
    }
}